@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:wght@700;900&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: white;
  font-family: "Barlow", sans-serif;
}

html {
  font-size: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Fraunces", serif;
}

a {
  text-decoration: none;
  outline: none;
}
a:visited {
  color: none;
}

ul {
  list-style-type: none;
}

p,
q {
  font-size: 1.125rem;
  color: #5a636c;
  line-height: 1.5;
}

img {
  width: 100%;
  height: 100%;
}

.header {
  padding: 1.875rem 1.25rem;
  background: url(../../../images/mobile/image-header.jpg) center no-repeat;
  background-size: cover;
  min-height: 75vh;
  text-align: center;
}
@media (min-width: 40em) {
  .header {
    min-height: 80vh;
  }
}
@media (min-width: 64em) {
  .header {
    padding: 1.875rem 2.5rem;
    min-height: 90vh;
  }
}
@media (min-width: 87.5em) {
  .header {
    min-height: 100vh;
  }
}
.header svg {
  transform: translateY(3.125rem);
}
@media (min-width: 64em) {
  .header svg {
    transform: translateY(5rem);
  }
}
@media (min-width: 87.5em) {
  .header svg {
    transform: translateY(7.5rem);
  }
}
.header__logo {
  width: 7.5rem;
}
.header__logo a:hover, .header__logo a:focus {
  border-bottom: 0.125rem solid rgba(255, 255, 255, 0.5);
}
.header__btn {
  position: relative;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  transition: all 250ms ease-in-out;
}
@media (min-width: 40em) {
  .header__btn {
    display: none;
  }
}
.header__btn::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 0;
  left: 50%;
  background: white;
  opacity: 0;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  transform: translateY(-50%) translateX(-50%);
  transition: opacity 250ms ease-in-out;
}
.header__btn:hover::after, .header__btn:focus::after, .header__btn:active {
  opacity: 0.3;
}
.header__title {
  margin-top: 5rem;
  color: white;
  font-size: 2.1875rem;
  letter-spacing: 0.4375rem;
  text-transform: uppercase;
  word-break: break-word;
}
@media (min-width: 64em) {
  .header__title {
    font-size: 3.75rem;
  }
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav .header__nav {
  position: absolute;
  top: 7.5rem;
  left: 5%;
  right: 5%;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2.5rem 0;
  visibility: hidden;
  opacity: 0;
  transition: 250ms ease-in-out;
  z-index: 999;
  box-shadow: 0 0 0.9375rem 0.0625rem rgba(0, 0, 0, 0.5);
}
@media (min-width: 40em) {
  nav .header__nav {
    visibility: visible;
    opacity: 1;
    background: transparent;
    position: relative;
    flex-direction: row;
    inset: unset;
    padding: 0;
    box-shadow: unset;
  }
}
nav .header__nav.active {
  visibility: visible;
  opacity: 1;
}
nav .header__nav::before {
  content: "";
  position: absolute;
  top: -1.5625rem;
  right: 0;
  width: 3.125rem;
  height: 3.125rem;
  background-color: white;
  clip-path: polygon(100% 0, 30% 50%, 100% 100%);
}
@media (min-width: 40em) {
  nav .header__nav::before {
    visibility: hidden;
    opacity: 0;
  }
}
nav .header__nav a {
  display: block;
  font-size: 1.125rem;
  color: #23303e;
  padding: 0.25rem 1.25rem;
}
@media (min-width: 40em) {
  nav .header__nav a {
    font-size: 1rem;
    color: white;
    padding: 0.625rem 0.9375rem;
  }
}
nav .header__nav_item:not(:last-child) {
  margin-bottom: 1.25rem;
}
@media (min-width: 40em) {
  nav .header__nav_item:not(:last-child) {
    margin-bottom: unset;
  }
}
@media (min-width: 64em) {
  nav .header__nav_item:not(:last-child) {
    margin-right: 1.875rem;
  }
}
nav .header__nav_item:not(:last-child) a {
  border-bottom: 0.125rem solid transparent;
  transition: 250ms ease-in-out;
}
nav .header__nav_item:not(:last-child) a:hover, nav .header__nav_item:not(:last-child) a:focus {
  border-bottom: 0.125rem solid #fad400;
}
@media (min-width: 40em) {
  nav .header__nav_item:not(:last-child) a:hover, nav .header__nav_item:not(:last-child) a:focus {
    border-bottom: 0.125rem solid rgba(255, 255, 255, 0.5);
  }
}
nav .header__nav_item:last-child a {
  text-transform: uppercase;
  font-family: "Fraunces", serif;
  font-weight: bold;
  background: #fad400;
  border: 0.125rem solid #fad400;
  border-radius: 1.25rem;
  transition: 250ms ease-in-out;
}
@media (min-width: 40em) {
  nav .header__nav_item:last-child a {
    border: none;
    background: white;
    color: #23303e;
    margin-left: 1.25rem;
  }
}
@media (min-width: 64em) {
  nav .header__nav_item:last-child a {
    margin-left: unset;
  }
}
nav .header__nav_item:last-child a:hover, nav .header__nav_item:last-child a:focus {
  background: transparent;
  color: #fad400;
}
@media (min-width: 40em) {
  nav .header__nav_item:last-child a:hover, nav .header__nav_item:last-child a:focus {
    background: rgba(255, 255, 255, 0.5);
    color: white;
  }
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas: "egg" "brand" "glass" "audience" "design" "photo";
  text-align: center;
  background: white;
}
@media (min-width: 64em) {
  .grid-container {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "brand egg" "glass audience" "design photo";
    text-align: left;
  }
}
.grid-container h2 {
  color: #5a636c;
  font-size: 1.875rem;
  font-weight: 900;
}
.grid-container h3 {
  font-size: 1.5625rem;
  font-weight: 900;
  text-transform: capitalize;
}
.grid-container p {
  padding: 1.5625rem 0.625rem;
}
@media (min-width: 40em) {
  .grid-container p {
    padding: 1.5625rem 0;
  }
}
.grid-container a {
  font-size: 1.125rem;
  font-family: "Fraunces", serif;
  text-transform: uppercase;
  color: #23303e;
  position: relative;
  z-index: 1;
}
.grid-container a:after {
  content: "";
  position: absolute;
  bottom: 0.0625rem;
  right: -0.4375rem;
  left: -0.4375rem;
  border-radius: 1.25rem;
  opacity: 0.3;
  z-index: -1;
  transition: 250ms ease-in-out;
}
.grid-container a:hover::after, .grid-container a:focus::after {
  opacity: 1;
}
.grid-container .egg {
  grid-area: egg;
}
.grid-container .brand {
  grid-area: brand;
  padding: 3.75rem 1.875rem;
  max-width: 27.1875rem;
  margin: auto;
}
.grid-container .brand a:after {
  border-bottom: 0.4375rem solid #fad400;
}
.grid-container .glass {
  grid-area: glass;
}
.grid-container .audience {
  grid-area: audience;
  padding: 3.75rem 1.875rem;
  max-width: 27.1875rem;
  margin: auto;
}
.grid-container .audience a:after {
  border-bottom: 0.4375rem solid #fe7867;
}
.grid-container .design {
  grid-area: design;
  background: url(../../../images/mobile/image-graphic-design.jpg) top/cover no-repeat;
}
.grid-container .design h3,
.grid-container .design p {
  color: #25564b;
}
@media (min-width: 40em) {
  .grid-container .design {
    background: url(../../../images/desktop/image-graphic-design.jpg) top/cover no-repeat;
  }
}
.grid-container .photo {
  grid-area: photo;
  background: url(../../../images/mobile/image-photography.jpg) top/cover no-repeat;
}
.grid-container .photo h3,
.grid-container .photo p {
  color: #19536b;
}
@media (min-width: 40em) {
  .grid-container .photo {
    background: url(../../../images/desktop/image-photography.jpg) top/cover no-repeat;
  }
}
.grid-container .container {
  max-width: 20.625rem;
  margin: 60vh auto 3.125rem;
}

.testimonials-container {
  max-width: 75rem;
  text-align: center;
  padding: 3.125rem 2.5rem;
  margin: auto;
}
@media (min-width: 64em) {
  .testimonials-container {
    padding: 9.375rem 2.5rem;
  }
}
.testimonials-container h4 {
  text-transform: uppercase;
  color: #a7abae;
  letter-spacing: 0.25rem;
}
@media (min-width: 64em) {
  .testimonials-container h4 {
    font-size: 1.5625rem;
  }
}
.testimonials-container .testimonials {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 3.125rem;
  margin-top: 3.75rem;
}
@media (min-width: 64em) {
  .testimonials-container .testimonials {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.1875rem;
  }
}
.testimonials-container .testimonials .client {
  display: flex;
  flex-direction: column;
  max-width: 21.875rem;
  margin: auto;
}
.testimonials-container .testimonials .client__img {
  width: 3.75rem;
  height: 3.75rem;
  margin: auto;
  border-radius: 50%;
}
@media (min-width: 64em) {
  .testimonials-container .testimonials .client__img {
    width: 5rem;
    height: 5rem;
  }
}
.testimonials-container .testimonials .client__quote {
  margin: 2.1875rem auto;
}
.testimonials-container .testimonials .client__name {
  font-size: 1.25rem;
  font-weight: 900;
}
.testimonials-container .testimonials .client__position {
  color: #a7abae;
  margin-top: 0.625rem;
  opacity: 0.7;
}

.image-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 64em) {
  .image-container {
    grid-template-columns: repeat(4, 1fr);
  }
}

.footer {
  background: rgba(69, 140, 126, 0.6);
  text-align: center;
  padding: 3.75rem 1.875rem;
}
.footer a {
  font-size: 1.125rem;
  color: #25564b;
  transition: 250ms ease-in-out;
}
.footer a:hover, .footer a:focus {
  color: white;
}
.footer a:focus svg, .footer a:hover svg {
  fill: white;
}
.footer a:focus i, .footer a:hover i {
  color: white;
}
.footer a:not(:last-child) {
  margin-right: 2.1875rem;
}
.footer a i {
  font-size: 1.5625rem;
  color: #25564b;
  transition: 250ms ease-in-out;
}
.footer svg {
  fill: #25564b;
  transition: 250ms ease-in-out;
  margin-right: -2.1875rem;
}
.footer .links {
  margin-top: 2.5rem;
}
.footer .social {
  margin-top: 3.125rem;
}